        role: 'Web Developer/Consultant',
        url: 'https://ruralsourcing.com',
      },
    ] as Experience[],
    educations: [
      {
        name: 'Augusta University',
        termLength: 'August 2014 - May 2018',
        descriptions: [
          'Bachelor of Science in Information Technology',
          'Minor in Computer Science',
